// Break point
@mixin breakpoint( $point ) {
	@if $point == sp {
		@media ( min-width: 480 ) {
			@content ;
		}
	}
	@if $point == table {
		@media ( min-width: 769px ) {
			@content ;
		}
	}
	@if $point == full {
		@media ( min-width: 1200px ) {
			@content ;
		}
	}
}

@mixin beforeafter {
	content: "";
	display: block;
}

@mixin clearfix {
	content: "";
	clear: both;
	display: block;
}

@mixin opacity( $opacity, $color01:#000 ) {
	background-color: rgba( $color01, $opacity );
}

@mixin fts($fts, $lin: 24) {
  $rem: $fts / 10;
  $linS: $lin / $fts;
  font-size: #{$fts}px;
  font-size: #{$rem}rem;
  line-height: $linS;
	color: $main_black;
}

@mixin title($fts) {
	font-size: $fts;
	font-weight: bold;
	color: $main_black;
}
