.Hero {
	height: 58rem;
	position: relative;
  background-repeat: no-repeat;
  background-size: cover;
	background-color: #0086A2;
  background-position: center center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
	position: relative;
	z-index: 10;
  &__Title{
    @include fts(40);
		color: #eee;
		font-family: "Open Sans",X-LocaleSpecific,sans-serif;
		font-weight: normal;
    margin-bottom: 5rem;
  }

  &__Catchcopy{
    @include fts(18);
		color: #eee;
    letter-spacing: .1rem;
    margin-bottom: 4rem;
  }

}

.aaa{
	position: absolute;
	right: 0;
	z-index: -1;
	svg{
		height: 80rem;
		width: 80rem;
		path{
			fill: rgba(#eee, 0.3);
		}
	}
}
