.Service {
	&__Container {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 5rem 0;
	}

	&__List {
		width: 100%;
		margin-top: 3rem;
		display: flex;
		align-items: center;
		justify-content: space-around;
	}

	&__Item {
		width: 30rem;
		display: flex;
		flex-direction: column;
		align-items: center;

		i {
			display: block;
			font-size: 4rem;
			margin-bottom: 1rem;
		}
	}

	&__Title {
		font-size: 2.4rem;
		margin-bottom: 1rem;
	}

	&__Description {
		font-size: 1.4rem;
		line-height: 1.3em;
	}
}
